<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
.car {
  display: block;
  position: relative;
}

.car img {
  left: 50%;
  opacity: 0.16;
  position: absolute;
  top: 0;
  transform: translate(-50%);
  width: 80%;
  z-index: 1;
  display: none;
}

.pedal {
  height: 15vh;
  width: 4vw;
  background-color: grey;
}
.pedal .empty {
  background-color: lightgrey;
  height: 100%;
  vertical-align: top;
  width: 100%;
}

/**/

.wheel {
  position: relative;
  border: 1px solid black;
  height: 20vh;
  width: 20vw;
  margin: 5vh 0;
}

.wheel .half {
  background-color: white;
  border: 1px solid black;
  font-size: 2em;
  height: 50%;
  line-height: 10vh;
  width: 100%;
}

.wheel .break {
  border: 1px solid lightgrey;
  font-size: 2em;
  margin-left: 5%;
  width: 90%;
}
.wheel .break .break-half {
  width: 48%;
}

.wheel .break-top {
  position: absolute;
  margin-top: -5vh;
}

.wheel .break-bottom {
  position: absolute;
  margin-top: 25vh;
}

.tercio {
  width: 30vw;
  display: inline-block;
}

.bloque-motor {
  border: 2px solid black;
  height: 50vh;
}

.bloque-delantero,
.bloque-trasero,
.bloque-motor {
  z-index: 2;
}

.engine {
  height: 30%;
  line-height: 15vh;
  font-size: 3em;
  width: 100%;
}

.engine .engine-half {
  width: 40%;
}

.cooler {
  margin-top: 8vh;
  font-size: 2.5em;
  width: 48%;
}

.cooler .cooler-temp {
  width: 100%;
}
</style>




